.Main-Container{
  
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 1%;
  font-size: 2vmin;
  color: white;
 
  
  
}

.CharacterCreate {
  
 
    width: fit-content;
    block-size: fit-content;
    height: fit-content;
    background: linear-gradient(90deg, #262b44 0%, #262b44 100%);
    border-radius: 5%;
    border: white;
    border-style: solid;
    display: flex;
    flex-direction: row;
    gap: 5px;
    align-items: center;
    justify-content: left;
    padding:40px;
    
  }

  
  
  .Character-Background{
   
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5%;
    border: white;
    border-style: solid;
    width:20vw;
    
    
  }
  
  .character{
    
    width:100%;
    position: relative;
    animation: updown 2s linear infinite;
    
  }
  
 .Asset-container{
   display:flex;
   flex-direction: column;
   align-self: flex-start;
   
  
 }
  .is-sith-button{
    margin-top: 2%;
    align-self: flex-start;
    font-family: 'SMW Text 2 NC', sans-serif;
    text-align: center;
    font-size: auto;
    color: white;
    border-radius: 20%;
    border-style: solid;
    border-color: white;
  }
  .is-sith-button:hover{
    cursor: pointer;
  }
  
  .Asset-manager-container{
    width:auto;
    padding: 2px;
    
    border-radius: 5%;
    border: white;
    border-style: solid;
    display: flex;
    flex-direction: column;
    
   
   
  }
  
  /* @keyframes spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  } */
  
  @keyframes updown{
    0%, 100% {
      bottom: 10px;
    }
    50% {
      bottom: 0px;
    }
  }
  